.sidebar {
    width: 25%;
    height: 100vh;
    background: #fdbc72;
    position: absolute;
    left: -30%;
    top: 0;
    transition: all 0.7s ease-out;
}

.open {
    left: 0%;
}

.sidebar-menus {
    width: 100%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
}